'use client';

import { useRouter } from 'next/navigation';
import { createClient } from '@/lib/supabase/client';
import { LogOut, Building2, TrendingUp, ShoppingCart, Users, Package, Clock, ChevronRight, BarChart3, DollarSign, Calendar, Mail, Phone } from 'lucide-react';
import { useState, useEffect } from 'react';

interface DashboardClientProps {
  user: any;
  profile: any;
  restaurants: any[];
}

export default function DashboardClient({ user, profile, restaurants }: DashboardClientProps) {
  const router = useRouter();
  const supabase = createClient();
  const [loading, setLoading] = useState(false);
  const [activeRestaurant, setActiveRestaurant] = useState<any>(null);
  const [stats, setStats] = useState({
    todayRevenue: 0,
    todayOrders: 0,
    activeOrders: 0,
    lowStockItems: 0
  });
  const [recentOrders, setRecentOrders] = useState<any[]>([]);
  const [loadingData, setLoadingData] = useState(true);

  useEffect(() => {
    if (restaurants.length > 0) {
      // Check localStorage for selected restaurant
      const savedRestaurantId = localStorage.getItem('selected_restaurant_id');
      if (savedRestaurantId) {
        const saved = restaurants.find(r => r.id === savedRestaurantId);
        if (saved) {
          setActiveRestaurant(saved);
        } else {
          setActiveRestaurant(restaurants[0]);
          localStorage.setItem('selected_restaurant_id', restaurants[0].id);
        }
      } else {
        setActiveRestaurant(restaurants[0]);
        localStorage.setItem('selected_restaurant_id', restaurants[0].id);
      }
    }

    // Listen for restaurant changes from other pages
    const handleRestaurantChange = () => {
      const savedRestaurantId = localStorage.getItem('selected_restaurant_id');
      if (savedRestaurantId && restaurants.length > 0) {
        const saved = restaurants.find(r => r.id === savedRestaurantId);
        if (saved && saved.id !== activeRestaurant?.id) {
          setActiveRestaurant(saved);
        }
      }
    };

    window.addEventListener('restaurantChanged', handleRestaurantChange);
    window.addEventListener('storage', handleRestaurantChange);

    return () => {
      window.removeEventListener('restaurantChanged', handleRestaurantChange);
      window.removeEventListener('storage', handleRestaurantChange);
    };
  }, [restaurants]);

  useEffect(() => {
    if (activeRestaurant) {
      loadDashboardData();
    }
  }, [activeRestaurant]);

  async function loadDashboardData() {
    if (!activeRestaurant) return;
    
    setLoadingData(true);
    try {
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      const tomorrow = new Date(today);
      tomorrow.setDate(tomorrow.getDate() + 1);

      // Get today's orders
      const { data: orders } = await supabase
        .from('orders')
        .select('*, order_items(*)')
        .eq('restaurant_id', activeRestaurant.id)
        .gte('created_at', today.toISOString())
        .lt('created_at', tomorrow.toISOString())
        .order('created_at', { ascending: false });

      if (orders) {
        const completedOrders = orders.filter(o => o.status === 'completed');
        const activeOrders = orders.filter(o => !['completed', 'cancelled'].includes(o.status));
        
        const todayRevenue = completedOrders.reduce((sum, order) => sum + (order.total || 0), 0);
        
        setStats({
          todayRevenue,
          todayOrders: completedOrders.length,
          activeOrders: activeOrders.length,
          lowStockItems: 0 // TODO: implement stock check
        });

        setRecentOrders(orders.slice(0, 5));
      }
    } catch (error) {
      console.error('Error loading dashboard data:', error);
    } finally {
      setLoadingData(false);
    }
  }

  const handleSignOut = async () => {
    setLoading(true);
    await supabase.auth.signOut();
    router.push('/');
    router.refresh();
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount);
  };

  const formatTime = (dateString: string) => {
    return new Date(dateString).toLocaleTimeString('id-ID', {
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getStatusColor = (status: string) => {
    const colors = {
      'pending': 'bg-yellow-100 text-yellow-800',
      'preparing': 'bg-blue-100 text-blue-800',
      'ready': 'bg-green-100 text-green-800',
      'completed': 'bg-gray-100 text-gray-800',
      'cancelled': 'bg-red-100 text-red-800'
    };
    return colors[status as keyof typeof colors] || 'bg-gray-100 text-gray-800';
  };

  if (restaurants.length === 0) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-[#FF5A5F]/5 via-white to-[#8B5CF6]/5 flex items-center justify-center">
        <div className="text-center max-w-md p-8">
          <Building2 className="w-20 h-20 text-gray-300 mx-auto mb-6" />
          <h2 className="text-3xl font-bold text-gray-900 mb-4">Belum Ada Bisnis</h2>
          <p className="text-gray-600 mb-8">
            Tambahkan bisnis pertama Anda untuk mulai menggunakan KadaiPOS
          </p>
          <button 
            className="px-8 py-4 bg-gradient-to-r from-[#FF5A5F] to-[#8B5CF6] text-white rounded-xl font-semibold hover:shadow-lg hover:scale-105 transition-all"
            onClick={() => router.push('/dashboard/setup')}
          >
            Tambah Bisnis
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <h1 className="text-2xl font-bold bg-gradient-to-r from-[#FF5A5F] to-[#8B5CF6] bg-clip-text text-transparent">
                KadaiPOS
              </h1>
              {restaurants.length > 1 && (
                <select 
                  value={activeRestaurant?.id || ''}
                  onChange={(e) => {
                    const selected = restaurants.find(r => r.id === e.target.value);
                    setActiveRestaurant(selected);
                    // Save to localStorage and dispatch event
                    localStorage.setItem('selected_restaurant_id', e.target.value);
                    window.dispatchEvent(new Event('restaurantChanged'));
                  }}
                  className="px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                >
                  {restaurants.map(r => (
                    <option key={r.id} value={r.id}>{r.name}</option>
                  ))}
                </select>
              )}
            </div>
            <div className="flex items-center gap-4">
              <span className="text-sm text-gray-600">{profile?.full_name || user.email}</span>
              <button
                onClick={handleSignOut}
                disabled={loading}
                className="flex items-center gap-2 px-4 py-2 text-sm font-medium text-gray-700 hover:text-[#FF5A5F] hover:bg-gray-50 rounded-xl transition-all disabled:opacity-50"
              >
                <LogOut className="w-4 h-4" />
                Keluar
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Welcome & Date */}
        <div className="mb-8">
          <h2 className="text-3xl font-bold text-gray-900 mb-2">
            Selamat datang, {profile?.full_name || 'Owner'}! ðŸ‘‹
          </h2>
          <p className="text-gray-600">
            {new Date().toLocaleDateString('id-ID', { 
              weekday: 'long', 
              year: 'numeric', 
              month: 'long', 
              day: 'numeric' 
            })}
          </p>
        </div>

        {loadingData ? (
          <div className="flex items-center justify-center py-20">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#FF5A5F]"></div>
          </div>
        ) : (
          <>
            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm hover:shadow-md transition-all">
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-[#FF5A5F]/10 rounded-xl flex items-center justify-center">
                    <DollarSign className="w-6 h-6 text-[#FF5A5F]" />
                  </div>
                </div>
                <h3 className="text-2xl font-bold text-gray-900 mb-1">
                  {formatCurrency(stats.todayRevenue)}
                </h3>
                <p className="text-sm text-gray-600">Pendapatan Hari Ini</p>
              </div>

              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm hover:shadow-md transition-all">
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-[#8B5CF6]/10 rounded-xl flex items-center justify-center">
                    <ShoppingCart className="w-6 h-6 text-[#8B5CF6]" />
                  </div>
                </div>
                <h3 className="text-2xl font-bold text-gray-900 mb-1">
                  {stats.todayOrders}
                </h3>
                <p className="text-sm text-gray-600">Pesanan Selesai</p>
              </div>

              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm hover:shadow-md transition-all relative">
                {stats.activeOrders > 0 && (
                  <div className="absolute -top-2 -right-2 w-6 h-6 bg-yellow-500 rounded-full flex items-center justify-center">
                    <span className="text-xs font-bold text-white">{stats.activeOrders}</span>
                  </div>
                )}
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-yellow-500/10 rounded-xl flex items-center justify-center">
                    <Clock className="w-6 h-6 text-yellow-600" />
                  </div>
                </div>
                <h3 className="text-2xl font-bold text-gray-900 mb-1">
                  {stats.activeOrders}
                </h3>
                <p className="text-sm text-gray-600">Pesanan Aktif</p>
              </div>

              <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm hover:shadow-md transition-all relative">
                {stats.lowStockItems > 0 && (
                  <div className="absolute -top-2 -right-2">
                    <div className="relative">
                      <div className="absolute inset-0 bg-red-500 rounded-full animate-ping opacity-75"></div>
                      <div className="relative w-6 h-6 bg-red-500 rounded-full flex items-center justify-center">
                        <span className="text-xs font-bold text-white">{stats.lowStockItems}</span>
                      </div>
                    </div>
                  </div>
                )}
                <div className="flex items-center justify-between mb-4">
                  <div className="w-12 h-12 bg-red-500/10 rounded-xl flex items-center justify-center">
                    <Package className="w-6 h-6 text-red-600" />
                  </div>
                </div>
                <h3 className="text-2xl font-bold text-gray-900 mb-1">
                  {stats.lowStockItems}
                </h3>
                <p className="text-sm text-gray-600">Stok Menipis</p>
              </div>
            </div>

            {/* Quick Actions */}
            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4 mb-8">
              <button
                onClick={() => router.push('/dashboard/analytics')}
                className="bg-white p-4 rounded-xl border border-gray-100 hover:border-[#FF5A5F] hover:shadow-md transition-all group cursor-pointer"
              >
                <BarChart3 className="w-8 h-8 text-[#FF5A5F] mb-2 mx-auto group-hover:scale-110 transition-transform" />
                <p className="text-sm font-medium text-gray-900">Analitik</p>
              </button>
              <button
                onClick={() => router.push('/dashboard/orders')}
                className="bg-white p-4 rounded-xl border border-gray-100 hover:border-[#8B5CF6] hover:shadow-md transition-all group cursor-pointer"
              >
                <ShoppingCart className="w-8 h-8 text-[#8B5CF6] mb-2 mx-auto group-hover:scale-110 transition-transform" />
                <p className="text-sm font-medium text-gray-900">Pesanan</p>
              </button>
              <button
                onClick={() => router.push('/dashboard/menu')}
                className="bg-white p-4 rounded-xl border border-gray-100 hover:border-green-500 hover:shadow-md transition-all group cursor-pointer"
              >
                <Package className="w-8 h-8 text-green-600 mb-2 mx-auto group-hover:scale-110 transition-transform" />
                <p className="text-sm font-medium text-gray-900">Menu</p>
              </button>
              <button
                onClick={() => router.push('/dashboard/staff')}
                className="bg-white p-4 rounded-xl border border-gray-100 hover:border-blue-500 hover:shadow-md transition-all group cursor-pointer"
              >
                <Users className="w-8 h-8 text-blue-600 mb-2 mx-auto group-hover:scale-110 transition-transform" />
                <p className="text-sm font-medium text-gray-900">Staff</p>
              </button>
              <button
                onClick={() => router.push('/dashboard/inventory')}
                className="bg-white p-4 rounded-xl border border-gray-100 hover:border-orange-500 hover:shadow-md transition-all group cursor-pointer"
              >
                <Package className="w-8 h-8 text-orange-600 mb-2 mx-auto group-hover:scale-110 transition-transform" />
                <p className="text-sm font-medium text-gray-900">Stok</p>
              </button>
              <button
                onClick={() => router.push('/dashboard/tables')}
                className="bg-white p-4 rounded-xl border border-gray-100 hover:border-purple-500 hover:shadow-md transition-all group cursor-pointer"
              >
                <Building2 className="w-8 h-8 text-purple-600 mb-2 mx-auto group-hover:scale-110 transition-transform" />
                <p className="text-sm font-medium text-gray-900">Meja</p>
              </button>
            </div>

            {/* Recent Orders */}
            <div className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-xl font-bold text-gray-900">Pesanan Terbaru</h3>
              </div>

              {recentOrders.length === 0 ? (
                <div className="text-center py-12">
                  <ShoppingCart className="w-16 h-16 text-gray-300 mx-auto mb-4" />
                  <p className="text-gray-500">Belum ada pesanan hari ini</p>
                </div>
              ) : (
                <div className="space-y-4">
                  {recentOrders.map((order) => (
                    <div
                      key={order.id}
                      className="flex items-center justify-between p-4 border border-gray-100 rounded-xl"
                    >
                      <div className="flex-1">
                        <div className="flex items-center gap-3 mb-2">
                          <span className="font-semibold text-gray-900">
                            Meja {order.table_id}
                          </span>
                          <span className={`px-3 py-1 rounded-full text-xs font-medium ${getStatusColor(order.status)}`}>
                            {order.status}
                          </span>
                        </div>
                        <p className="text-sm text-gray-600">
                          {order.order_items?.length || 0} item â€¢ {formatTime(order.created_at)}
                        </p>
                      </div>
                      <div className="text-right">
                        <p className="font-bold text-gray-900">
                          {formatCurrency(order.total || 0)}
                        </p>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </>
        )}
      </main>
    </div>
  );
}
